import React from 'react';
import './AboutDescription.scss';

class AboutDescription extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			expanded: false
		};

	}

	onLinkClicked(event) {
		event.preventDefault();
		const { expanded } = this.state;
		this.setState({
			expanded: !expanded
		});

	}

	getMoreTextDiv() {
			if (this.state.expanded) {
				return <div> <p>Wanie w tym roku (2017) zostaem absolwentem studiw informatycznych na UwB. Ten kierunek studiw wybraem nie bez powodu, poniewa od 16 roku ycia nie rozstaje si
	z komuterem odkrywajc wci nowe moliwoci i technologie, ktre oferuje nam ta dziedzina. Gdy budowaem pierwsze strony internetowe jeszcze w czasach, gdy wykorzystywano
do tego tabelki, wiedziaem ju, e technologie internetowe to bdzie co co chc w yciu robi. Nieustajcy szybki rozwj technologii jeszcze bardziej inspiruje i zachca do dziaania.
Do tej pory udao mi si pozna bardzo wiele jzykw, narzdzi, sposbow do budowania aplikacji i serwisw internetowych. Nie jestem ekspertem z adnej dziedzin programowania, jednak
przez swoja "karier" udao mi si pozna bardzo wiele rnorodnych technologii oraz rozwiaza. Dziki temu, dzi potrafie odpowiedzie na takie pytania jak: W jakim jzku chciabym
si rozwija. JavaScript to wybr na ktry postawiem stosunkowo do niedawno, wybr z ktrego czerpi duo satysfakcji i motywacji !
</p>
<p>
Na codzie jestem energiczn osob. Kady swj dzie staram si dokadnie planowa i przez wiele lat wypracowaem metody organizacji i samodyscypliny, ktre s skuteczne i efektywne. Zaplanowane zadania wykonuj
konsekwentnie i starannie. Jestem odporny na stres, dug prac oraz radz sobi z natokiem prac. Wolny czas staram si spdza uprawiajc sport. Jestem osob otwart, komunikatywn i nastawion na wspprac.
</p>
</div>;
			} else {
				return null;
			}
	}

	render(){
		const expandedDiv = this.getMoreTextDiv();
		const buttonContent = this.state.expanded ? 'Ukryj' : 'Czytaj wicej';
		//const containerClass = this.state.expanded ? {Wiecej} : ''
		return(
			<div className="AboutDescription">
				<h2>O mnie</h2>
				<p>Ciesz si, e trafie na moj personaln stron ! :) </p>
				<p>Skoro to czytasz to prawdopodobnie chcesz czego dowiedzie si o mnie. A wic, postaram si opisa moj histori zawodow jak i osobist. </p>
				{expandedDiv}
				<a href="/" onClick={this.onLinkClicked.bind(this)}>
						<button>{buttonContent}</button>
				</a>
			</div>
		);
	}
}
export default AboutDescription;



