import React from 'react';

import './SectionExperience.scss';
import HeadSection from '../../common/headSection/HeadSection.js';
import {Timeline, TimelineEvent} from 'react-event-timeline';
class SectionExperience extends React.Component{

	componentWillMount() {
		//document.body.style.backgroundColor = "blue";
	}
	render(){

		return(
			<div className="SectionExperience">
				<HeadSection name="Dowiadczenie" />
					<Timeline>
									<TimelineEvent title="Future Trip Sp. z o.o."
																 createdAt="07/2016 - 04/2017"
																 icon={<i className="Odd"></i>}
									>
										 Praca nad projektem, tworzcym rozwiazania dla przewonikw autobusowych. Wykorzystywane technologie i narzedzia: HTML, CSS, Bootstrap, PYTHON, DJANGO, REST API, REACT, REDUX, ES6
									</TimelineEvent>
									<TimelineEvent title="Logos Centrum Edukacyjne oma"
																 createdAt="02/2017"
																 icon={<i className="Odd"></i>}
									>
										 Prowadzenie kursu dla uczniw szkoy redniej z podstaw programowania w Ostroce.
									</TimelineEvent>
									<TimelineEvent title="OpsTalent Sp. z o.o."
																 createdAt="05/2016 - 11/2016"
																 icon={<i className="Odd"></i>}
									>
											Praca przy budowaniu serwisw dla zagranicznych klientw m.in serwis papilly.com. Wykorzystywane technologie i narzedzia: CSS, Bootstrap, HTML, PHP, GIT, nginx, SCRUM, Wordpres
									</TimelineEvent>
									<TimelineEvent title="ESL Landscape Contractors Londyn"
																 createdAt="06/2015 - 09/2016"
																 icon={<i className="Odd"></i>}
									>
											Praca polegaa na projektowaniu i realizacji ogrodw na obiektach rozmieszczonych po Londynie.
									</TimelineEvent>
									<TimelineEvent
											title="Dynamic Solutions Sp. z o.o. Biaystok"
											createdAt="11/2014 - 04/2015"
											icon={<i className="Odd"></i>}
									>
									Praca przy budowie serwisu dla biura nieruchomoci ktry zintegrowano z zewntrznym API. Wykorzystywane technologie: PHP, REST API, jQuery, HTML, CSS, RWD, SVN.
									</TimelineEvent>
									<TimelineEvent
											title="Broker Media Warszawa"
											createdAt="2012r - 2014r"
											icon={<i className="Odd"></i>}
									>
									Realizacja projektw opartych o CMS wordpress. Wykorzystywane technologie: HTML, CSS, jQuery, Wordpress.
									</TimelineEvent>
									<TimelineEvent
											title="Praca jako freelancer"
											createdAt="2008r - 2012r"
											icon={<i className="Odd"></i>}
									> Wsppraca z firmami takimi jak: Bopss Suwaki, Wenus Meble, It-Service. Sudnikowski P
									Drobne projekty, fora internetowe, projektowanie grafik. Wykorzystywane technologie i narzedzia: HTML, CSS, PHP, phpmybb, Webspell, ActionScript, Flash, Photoshop.

									</TimelineEvent>
					</Timeline>,
			</div>
		);
	}
}
export default SectionExperience;



